<template>
	<div class="page-register">
		<el-row>
			<el-col :span="8" :offset="8">
				<el-alert title="" type="success" center></el-alert>
				<div class="login-form">
					<!--  -->
					<div class="login-header">
						<h2 class="title" style="margin:0"></h2>
					</div>
					<!-- // -->
					<!--  -->
					<el-form
						:label-position="labelPosition"
						label-width="66px"
						:model="formRegister"
						:rules="rulesRegister"
						ref="formRegister"
					>
						<el-form-item label="" size="small" prop="username">
							<el-input
								v-model="formRegister.username"
								placeholder="3~10"
								clearable
								class="input"
								autocomplete="off"
							></el-input>
						</el-form-item>
						<el-form-item label="" size="small" prop="email">
							<el-input
								v-model="formRegister.email"
								placeholder=""
								clearable
								class="input"
							>
								<el-button
									slot="append"
									style="background-color:rgb(240,249,235)"
									@click.prevent="sendcode()"
									:disabled="isCodeSend"
									>{{ sendBtnText }}</el-button
								>
							</el-input>
						</el-form-item>
						<el-form-item label="" size="small" prop="code">
							<el-input
								v-model="formRegister.code"
								placeholder=""
								clearable
								class="input"
							>
							</el-input>
						</el-form-item>
						<el-form-item label="" size="small" prop="pwd">
							<el-input
								v-model="formRegister.pwd"
								placeholder=""
								clearable
								show-password
								class="input"
								autocomplete="off"
							></el-input>
						</el-form-item>
						<el-button type="primary" @click="submitForm('formRegister')" class="btn-login"
							></el-button
						>
					</el-form>
					<!-- // -->

					<!--  -->
					<div class="tips">
						
						<a href="#" style="text-decoration:none; color:dodgerblue"></a><a
							></a
						>
						<div style="margin-top:5px">
							
							<router-link :to="{ name: 'Login' }" style="text-decoration:none; color:dodgerblue"
								>></router-link
							>
						</div>
					</div>
					<!-- : -->
					<div class="login-footer">
						<span href="#">ICP 10002670012</span>
					</div>
					<!-- //: -->
				</div>
			</el-col>
		</el-row>
	</div>
</template>

<script>
import { ajax } from '@/utils/ajax'
import { UserApis } from '@/utils/apis'
import { CREATED, TWO_MANY_REQUESTS } from '@/utils/constants'

export default {
	data() {
		var checkEmail = (rule, value, callback) => {
			const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
			if (!value) {
				return callback(new Error(''))
			}
			setTimeout(() => {
				if (mailReg.test(value)) {
					callback()
				} else {
					callback(new Error(''))
				}
			}, 100)
		}
		var checkUname = (rule, value, callback) => {
			const mailReg = /^[a-zA-Z]{3,10}$/
			if (!value) {
				return callback(new Error(''))
			}
			setTimeout(() => {
				if (mailReg.test(value)) {
					callback()
				} else {
					callback(new Error('(3~10)'))
				}
			}, 100)
		}
		return {
			isCodeSend: false,
			sendBtnText: '',
			counter: 60,
			timer: null,
			labelPosition: 'left',
			formRegister: {
				username: '',
				email: '',
				code: '',
				pwd: '',
			},
			rulesRegister: {
				username: [
					{ validator: checkUname, trigger: 'blur' },
					{ required: true, message: '', trigger: 'blur' },
					{ min: 3, max: 10, message: '3~10', trigger: 'blur' },
				],
				email: [
					{ validator: checkEmail, trigger: 'blur' },
					{ required: true, message: '', trigger: 'blur' },
				],
				code: [{ required: true, message: '', trigger: 'blur' }],
				pwd: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 4, max: 17, message: '4~17', trigger: 'blur' },
				],
			},
		}
	},
	methods: {
		// 
		countDown() {
			this.timer = setInterval(() => {
				this.sendBtnText = `(${this.counter})`
				this.counter--
				if (this.counter < 0) {
					this.reset()
				}
			}, 1000)
		},
		send_code() {
			ajax
				.post(UserApis.sendCodeUrl, {
					email: this.formRegister.email,
				})
				.then(
					(res) => {
						if (res.data.code === CREATED) {
							this.$message.success('')
							this.code = res.data.body.verify_code
							console.log(this.code)
						}
					},
					(error) => {
						if (error.data.code === TWO_MANY_REQUESTS) {
							this.$message({
								showClose: true,
								message: '',
								type: 'error',
							})
						}
						this.formRegister.email = ''
					}
				)
		},
		// 
		reset() {
			this.isCodeSend = false
			this.sendBtnText = ''
			if (this.timer) {
				clearInterval(this.timer)
				this.counter = 60
				this.timer = null
			}
		},
		sendcode() {
			// 
			if (!this.formRegister.email) {
				// this.$notify('')
				return false
			}
			this.isCodeSend = true
			// 
			this.send_code()
			// 60s
			this.countDown()
		},
		Authication() {
			ajax
				.post(UserApis.userRegisterUrl, {
					username: this.formRegister.username,
					email: this.formRegister.email,
					verify_code: this.formRegister.code,
					password: this.formRegister.pwd,
				})
				.then(
					(res) => {
						if (res.data.code === CREATED) {
							console.log(res.data.body.access)
							window.localStorage.setItem('access', 'Bearer ' + res.data.body.access)
							this.$message.success('')
							// this.$store.commit('updateUserinfo', res.data)
							this.$router.push({ name: 'Home' })
						}
					},
					(error) => {
						this.formRegister.username = ''
						this.formRegister.email = ''
						this.formRegister.code = ''
						this.formRegister.pwd = ''
					}
				)
		},
		submitForm(formRegister) {
			this.$refs[formRegister].validate((valid) => {
				if (valid) {
					this.Authication()
				} else {
					console.log('')
					return false
				}
			})
		},
	},
}
</script>

<style lang="less">
.page-register {
	text-align: center;
	.el-col {
		overflow-y: hidden;
	}
	.login-form {
		margin-top: 40px;
		background-color: rgb(246, 248, 250);
		border-radius: 20px;
		box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
		padding: 10px 30px;
	}
	.login-header {
		text-align: center;
		margin-bottom: 25px;
	}
	.login-header .title {
		color: dodgerblue;
		font-size: 25px;
	}
	.tips {
		margin-top: 20px;
		font-size: 14px;
	}
	.btn-login {
		border-radius: 20px;
	}
	.el-form-item__content {
		margin-left: 0;
	}
	.login-footer {
		margin-top: 20px;
		color: rgb(150, 143, 143);
		text-align: center;
	}
	.login-footer span {
		padding: 15px;
		font-size: 12px;
	}
}
</style>
